<template>
  <div class="hello">
    <div class="houses-head-wrap">
      <el-button type="primary" size="small" round @click="handleToAdd"></el-button>
    </div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        fixed
        prop="number"
        label=""
        width="80px">
      </el-table-column>
      <el-table-column
        prop="type"
        label=""
        width="min-width">
      </el-table-column>
      <el-table-column
        prop="address"
        label=""
        :filters="[{text: '32', value: '32'}, {text: '5', value: '5'}, {text: '', value: ''}]"
        :filter-method="filterHandler"
        width="min-width">
      </el-table-column>
      <el-table-column
        fixed="right"
        width="50px"
        label="">
        <template slot-scope="scope">
          <el-dropdown trigger="click" @command="handleNmenuClick">
            <i class="el-icon-more"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item :command="{house:scope.row,action:'records'}"></el-dropdown-item>
              <el-dropdown-item :command="{house:scope.row,action:'history'}"></el-dropdown-item>
              <el-dropdown-item :command="{house:scope.row,action:'edit'}"></el-dropdown-item>
              <el-dropdown-item :command="{house:scope.row,action:'delete'}"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        <!--<el-button
          size="mini"
          v-on:click="handleEdit(scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          v-on:click="handleDelect(scope.row)"></el-button>-->
    </template>
  </el-table-column>
    </el-table>
  </div>
</template>

<script>
import Utils from '../utils.js';
export default {
  name: 'HelloWorld',
  data() {
    return {
      inputdata: '',
      tableData: [],
      pagetitle: '',
    };
  },
  props: {
    msg: String,
  },
  beforeRouteEnter (to, form, next) {
    next((vm) => {
      vm.$store.commit('settitle', vm.pagetitle);
    });
  },
  created() {
    const ts = Date.now();
    const that = this;
    window.firebase.database().ref('houses').on('value', (dataSnapshot) => {

      that.tableData = Utils.Object2Array(dataSnapshot.val());
      console.log(Date.now()-ts);
    });
  },
  methods: {
    handleClick() {
      const temparray = this.tableData ? this.tableData.map(i => i) : [];
      temparray.push({
        number: this.inputdata,
        type: '',
        address: '32-1',
      });
      window.firebase.database().ref('test').set(temparray);
      // console.log(window.firebase);
      // console.log(this.inputdata);
    },
    handleDelect(a) {
      this.$confirm(` ${a.number}-${a.type}-${a.address} ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then( () => {
          window.firebase.database().ref('houses/' + a.id).remove();
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch( () => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

    },
    handleEdit(a) {
      this.$router.push({
        path: '/edithouse/'+a.id,
      });
    },
    handleToAdd() {
      this.$router.push({
        path: '/add',
      });
    },
    handleNmenuClick (c) {
      switch (c.action) {
        case 'records': this.$store.commit('sethouse', c.house);this.$store.commit('setwatermonitors', c.house.watermonitors);
        this.$store.commit('seteletricmonitors', c.house.eletricmonitors);this.$router.push({path: '/managerecord'});break;
        case 'history': this.$router.push({path: '/history'});this.$store.commit('sethouse', c.house);break;
        case 'edit': this.handleEdit(c.house); break;
        case 'delete': this.handleDelect(c.house); break;
      }
    },
    filterHandler (value,row) {
      return row.address === value;
    },
  },
};
</script>

